<template><div class="content"><h1 class="post-title">一个简单的初级登录系统<a class="header-anchor" id="%E4%B8%80%E4%B8%AA%E7%AE%80%E5%8D%95%E7%9A%84%E5%88%9D%E7%BA%A7%E7%99%BB%E5%BD%95%E7%B3%BB%E7%BB%9F" href="#%E4%B8%80%E4%B8%AA%E7%AE%80%E5%8D%95%E7%9A%84%E5%88%9D%E7%BA%A7%E7%99%BB%E5%BD%95%E7%B3%BB%E7%BB%9F">#</a></h1><div class="metabar"><div class="metabar-item">2021/05/20</div><div class="metabar-item">代码</div><div class="metabar-item">约 12 分钟读完</div></div>
<p>先前一直在想登录系统到底该怎样简洁地实现。在这里，<em>简洁</em>是指能够满足最基本的用户识别需求，而没有其它更多方面的需求，例如验证方面、安全方面等等。因而概括来说，我们所要实现的只是
登录以后储存状态，根据该状态判定访问权限。</p>
<div class="notice tip"><p>🐘 本文代码使用 PHP 实现。</p>
</div>
<h2>基本思路<a class="header-anchor" id="%E5%9F%BA%E6%9C%AC%E6%80%9D%E8%B7%AF" href="#%E5%9F%BA%E6%9C%AC%E6%80%9D%E8%B7%AF">#</a></h2>
<p>这样一个简单的登录系统的思路，大致如下</p>
<ol>
<li><strong>前端收集登录信息。</strong> 包含最基本的两项：用户名和密码。</li>
<li><strong>后端验证信息并发放 <em>Token</em>。</strong> 在这里，验证信息主要是对数据库中对应用户名的密码进行比对，可能的结果有<em>不存在</em>、<em>不匹配</em>和<em>匹配</em>三种。本文中比对所使用的方式是 <code>password_verify</code> 函数。</li>
<li><strong>前端存储 <em>Token</em> 并用于执行各项操作。</strong> 后端所需要权限的操作可以将 Token 作为参数，需要 Token 才能工作。前端存储 Token 的办法，可以用 <code>localStorage</code>。当然，使用 Cookie 也不是不可以。本文演示前者，实现方式类似。</li>
<li><strong>针对不同状况拟定 <em>Token</em> renew &amp; expire 策略。</strong> Token 一定要有有效期，自然而然也有 renew 和 expire 等问题。</li>
</ol>
<p>上面四条，我们接下来注意解释和实践。</p>
<h2>实践<a class="header-anchor" id="%E5%AE%9E%E8%B7%B5" href="#%E5%AE%9E%E8%B7%B5">#</a></h2>
<h3>1. Token 设计</h3>
<p>Token 到底应该长什么样？实际上应该先讨论我们该怎样传递 Token。为了安全性，我们必定要设计一些加密（encrypt）方法。为了信息可处理，我们也需要思考怎样进行解密（decrypt）。在这个地方我们当然不能用简简单单的 base64 混合其它花里胡哨方法的肤浅加密，而要使用 OpenSSL 的相关方法。</p>
<p>首先，OpenSSL 的加密函数为 <code>openssl_encrypt</code>，利用这个函数我们可以封装出一个简单的实际 encrypt。</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">encrypt</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$key</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
    <span class="token variable">$nonceSize</span> <span class="token operator">=</span> <span class="token function">openssl_cipher_iv_length</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'aes-256-ctr'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token variable">$nonce</span> <span class="token operator">=</span> <span class="token function">openssl_random_pseudo_bytes</span><span class="token punctuation">(</span><span class="token variable">$nonceSize</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

    <span class="token variable">$ciphertext</span> <span class="token operator">=</span> <span class="token function">openssl_encrypt</span><span class="token punctuation">(</span>
        <span class="token variable">$message</span><span class="token punctuation">,</span>
       <span class="token string single-quoted-string">'aes-256-ctr'</span><span class="token punctuation">,</span>
        <span class="token variable">$key</span><span class="token punctuation">,</span>
        <span class="token constant">OPENSSL_RAW_DATA</span><span class="token punctuation">,</span>
        <span class="token variable">$nonce</span>
    <span class="token punctuation">)</span><span class="token punctuation">;</span>
    
    <span class="token keyword">return</span> <span class="token variable">$nonce</span> <span class="token operator">.</span> <span class="token variable">$ciphertext</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>其中，<strong>aes-256-ctr</strong> 为加密方式，具体可以自行查询了解，这是比较推荐使用的一种方式。上面的函数具体内容就不多解释。利用上面的函数，只需要一个 key 即可对指定信息进行加密，这些信息便是我们的 Token。</p>
<p>针对上面的 encrypt 函数，我们也有一个对应的 decrypt 函数。</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">decrypt</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$key</span><span class="token punctuation">)</span>
    <span class="token punctuation">{</span>
        <span class="token variable">$nonceSize</span> <span class="token operator">=</span> <span class="token function">openssl_cipher_iv_length</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'aes-256-ctr'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token variable">$nonce</span> <span class="token operator">=</span> <span class="token function">mb_substr</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token variable">$nonceSize</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'8bit'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token variable">$ciphertext</span> <span class="token operator">=</span> <span class="token function">mb_substr</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$nonceSize</span><span class="token punctuation">,</span> <span class="token constant">null</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'8bit'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token variable">$plaintext</span> <span class="token operator">=</span> <span class="token function">openssl_decrypt</span><span class="token punctuation">(</span>
            <span class="token variable">$ciphertext</span><span class="token punctuation">,</span>
            <span class="token string single-quoted-string">'aes-256-ctr'</span><span class="token punctuation">,</span>
            <span class="token variable">$key</span><span class="token punctuation">,</span>
            <span class="token constant">OPENSSL_RAW_DATA</span><span class="token punctuation">,</span>
            <span class="token variable">$nonce</span>
        <span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token keyword">return</span> <span class="token variable">$plaintext</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>而实际上，这两个函数都并不完全安全。在后文，我们会介绍更为安全的版本。</p>
<p>这两函数便负责了加密和解密。接下来 Token 内容的组织便很容易了。我们可以使用最简单的，那就是 JSON。例如</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token variable">$token</span> <span class="token operator">=</span> <span class="token function">json_encode</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token string double-quoted-string">"expires"</span> <span class="token operator">=&gt;</span> <span class="token function">strtotime</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"+1 week"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token string double-quoted-string">"creates"</span> <span class="token operator">=&gt;</span> <span class="token function">time</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre></div>
<p>对这个 Token，我们进行 encrypt，然后返回到前端，基本上就没有什么问题了。这个 Token 在前端将长久存在，因而其必须指定一个合理的 expires 以便后端判断其有效性。同时为了避免冒充，我们还可以在 Token 内加入一些特异性内容，例如字符串签名。</p>
<h3>Token 存储</h3>
<p>按照设计，Token 被返回到前端以后会被存储起来，目的是作为敏感操作（涉及权限的操作）的凭证。例如若要 POST 涉及权限的操作，则必须带上 Token，否则返回 400。</p>
<p>上文已经提到，我们将会使用 localStorage 或者 Cookie。例如</p>
<div class="language-javascript"><pre class="language-javascript"><code class="language-javascript"><span class="token comment">// 假设 r 为后端返回的 Token（已加密字符串）</span>
localStorage<span class="token punctuation">.</span><span class="token function">setItem</span><span class="token punctuation">(</span><span class="token string">'xxx-login-token'</span><span class="token punctuation">,</span> r<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre></div>
<p>至此它就被存了起来。至于 localStorage 保存数据的持续性，可以参考 StackOverflow <a href="https://stackoverflow.com/questions/9948284/how-persistent-is-localstorage" class="external-link" target="_blank" rel="noopener noreferrer">How Persistent is localStorage?<span class="external-link-icon mdi mdi-launch"></span></a>。</p>
<p>对于 Chrome，localStorage 持续性的叙述为</p>
<blockquote>
<p>On disk until deleted by user (delete cache) or by the app.</p>
</blockquote>
<p><small>来自 <router-link to="http://www.gwtproject.org/doc/latest/DevGuideHtml5Storage.html">GWTProject</router-link></small></p>
<p>所以实际上不需要担心 localStorage 被无缘无故清掉。换句话说，Token 将会在 localStorage 中长期存在。</p>
<p>在这里出现了一个问题：localStorage 在定义上是属于用户的东西，用户可以完全管理其内容，可以添加、读取和修改其内容，这会对 Token 的安全性造成什么影响？</p>
<p>实际上基本没有。因为 Token 只能由后端生成，而用户把 Token 修改了相当于自己放弃了这个 Token，被修改后的 Token 也无法通过验证（decrypt）。唯一需要防备的，就是不要泄漏 Token 的加密方式。如果加密方式泄漏，那么用户就可以利用各种方法采取完全相同的加密方式加密出完全符合服务端解密函数需求的 Token。在这里，加密方式包含的不仅仅是 method 本身，还有 key。</p>
<h3>Token 利用</h3>
<p>Token 的利用主要体现在请求上，且有多种。在这里我们演示最为简单的一种：每一次 POST 敏感操作接口，附带上 Token 作为参数，后端在接到请求后，第一步是验证 Token，通过后继续操作。</p>
<p>比如，每次 route 跳转到敏感页面，我们就可以执行这样一个函数：</p>
<div class="language-typescript"><pre class="language-typescript"><code class="language-typescript"><span class="token keyword">function</span> <span class="token function">checkAuth</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token keyword">let</span> token <span class="token operator">=</span> localStorage<span class="token punctuation">.</span><span class="token function">getItem</span><span class="token punctuation">(</span><span class="token string">"xxx-login-token"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
  <span class="token keyword">return</span> <span class="token keyword">new</span> <span class="token class-name"><span class="token builtin">Promise</span></span><span class="token punctuation">(</span><span class="token punctuation">(</span>r<span class="token punctuation">,</span> j<span class="token punctuation">)</span> <span class="token operator">=&gt;</span> <span class="token punctuation">{</span>
    <span class="token keyword">if</span> <span class="token punctuation">(</span><span class="token operator">!</span>token<span class="token punctuation">)</span> <span class="token punctuation">{</span>
      <span class="token function">j</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span> <span class="token keyword">else</span> <span class="token punctuation">{</span>
      <span class="token function">post</span><span class="token punctuation">(</span>
        <span class="token string">"/CheckAuth.php"</span><span class="token punctuation">,</span>
        <span class="token punctuation">{</span>
          token<span class="token punctuation">,</span>
        <span class="token punctuation">}</span><span class="token punctuation">,</span>
        <span class="token punctuation">(</span>response<span class="token punctuation">)</span> <span class="token operator">=&gt;</span> <span class="token punctuation">{</span>
          <span class="token keyword">if</span> <span class="token punctuation">(</span>response <span class="token operator">===</span> <span class="token boolean">true</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
	        <span class="token function">r</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
          <span class="token punctuation">}</span> <span class="token keyword">else</span> <span class="token punctuation">{</span>
            <span class="token function">j</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
          <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>
      <span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
  <span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>这个函数会获取 localStorage 中的 Token 并发送给后端相应的接口，通过验证，返回一个结果值，前端再根据这个结果值决定是否跳转到指定的页面。</p>
<p>至于 Token 怎样验证：</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">checkToken</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$token</span><span class="token punctuation">)</span>
<span class="token punctuation">{</span>
    <span class="token variable">$key</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'...'</span><span class="token punctuation">;</span> <span class="token comment">// 提供加密时的 key 以解密</span>
    <span class="token keyword">try</span> <span class="token punctuation">{</span>
        <span class="token variable">$content</span> <span class="token operator">=</span> <span class="token function">json_decode</span><span class="token punctuation">(</span><span class="token function">decrypt</span><span class="token punctuation">(</span><span class="token variable">$token</span><span class="token punctuation">,</span> <span class="token variable">$key</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">if</span> <span class="token punctuation">(</span><span class="token variable">$content</span><span class="token punctuation">[</span><span class="token string double-quoted-string">"expires"</span><span class="token punctuation">]</span> <span class="token operator">&gt;</span> <span class="token function">time</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token keyword">return</span> <span class="token constant boolean">true</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span> <span class="token keyword">else</span> <span class="token punctuation">{</span>
            <span class="token keyword">return</span> <span class="token constant boolean">false</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span> <span class="token keyword">catch</span> <span class="token punctuation">(</span><span class="token class-name">Exception</span> <span class="token variable">$e</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
        <span class="token keyword">return</span> <span class="token constant boolean">false</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre></div>
<p>具体应用中还可以添加更多验证方式在里面。验证成功，就会返回一个 true。如果过期，那么就会直接返回 false。如果是无效的 Token，根据上面的 encrypt 和 decrypt 函数，会产生一个 Exception。如果产生这个 Exception，就代表通过验证的一个或多个必要条件不满足，自然就能判定这个 Token 无效。</p>
<h3>Token renew &amp; expire 策略</h3>
<p>如果 Token 过期了，我们将把它当作无效 Token 同等处理，即 <code>checkToken(x)</code> 函数永远返回 false。</p>
<p>至于 Token 的更新以及销毁，前者会发生在登录时。由于 Token 已经被（近乎）永远存在了前端，所以过期的 Token 并没有必要直接删除。每次登录的时候，如果 Token 没有过期，那么直接跳转到相关页面，如果 Token 过期了，那么就返回一个新的 Token。执行 localStorage 的 setItem 语句直接覆盖旧 Token 即可。而对于销毁，则是用户专门在 Token 有效的时候执行了相关的操作，这个时候就不需要和后端沟通，而前端直接删去 localStorage 中 Token 对应的数据即可，由于前面路由逻辑的存在，页面刷新以后一切就跟没有登录一样了。</p>
<h3>更安全的 encrypt 和 decrypt</h3>
<p>更安全的 encrypt 是将原先不安全函数的加密结果的开头加上一个计算出的 MAC 值。</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">encrypt</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$key</span><span class="token punctuation">)</span>
    <span class="token punctuation">{</span>
        <span class="token keyword">list</span><span class="token punctuation">(</span><span class="token variable">$encKey</span><span class="token punctuation">,</span> <span class="token variable">$authKey</span><span class="token punctuation">)</span> <span class="token operator">=</span> <span class="token function">splitKeys</span><span class="token punctuation">(</span><span class="token variable">$key</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token variable">$ciphertext</span> <span class="token operator">=</span> <span class="token function">unsafe_encrypt</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$encKey</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token comment">// 计算</span>
        <span class="token variable">$mac</span> <span class="token operator">=</span> <span class="token function">hash_hmac</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token variable">$ciphertext</span><span class="token punctuation">,</span> <span class="token variable">$authKey</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        
        <span class="token comment">// 把 MAC 放在开头</span>
        <span class="token keyword">return</span> <span class="token variable">$mac</span> <span class="token operator">.</span> <span class="token variable">$ciphertext</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
</code></pre></div>
<p>更安全的解密也是基于 MAC 的。在这里我们先验证 MAC，然后再使用先前的解密函数即可。</p>
<div class="language-php"><pre class="language-php"><code class="language-php">    <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">function</span> <span class="token function-definition function">decrypt</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$key</span><span class="token punctuation">)</span>
    <span class="token punctuation">{</span>
        <span class="token keyword">list</span><span class="token punctuation">(</span><span class="token variable">$encKey</span><span class="token punctuation">,</span> <span class="token variable">$authKey</span><span class="token punctuation">)</span> <span class="token operator">=</span> <span class="token function">splitKeys</span><span class="token punctuation">(</span><span class="token variable">$key</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token comment">// 避免加密算法变换</span>
        <span class="token variable">$hs</span> <span class="token operator">=</span> <span class="token function">mb_strlen</span><span class="token punctuation">(</span><span class="token function">hash</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'8bit'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token variable">$mac</span> <span class="token operator">=</span> <span class="token function">mb_substr</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token number">0</span><span class="token punctuation">,</span> <span class="token variable">$hs</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'8bit'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token variable">$ciphertext</span> <span class="token operator">=</span> <span class="token function">mb_substr</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">,</span> <span class="token variable">$hs</span><span class="token punctuation">,</span> <span class="token constant">null</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'8bit'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token variable">$calculated</span> <span class="token operator">=</span> <span class="token function">hash_hmac</span><span class="token punctuation">(</span>
            <span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span>
            <span class="token variable">$ciphertext</span><span class="token punctuation">,</span>
            <span class="token variable">$authKey</span><span class="token punctuation">,</span>
            <span class="token constant boolean">true</span>
        <span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token keyword">if</span> <span class="token punctuation">(</span><span class="token operator">!</span><span class="token function">hashEquals</span><span class="token punctuation">(</span><span class="token variable">$mac</span><span class="token punctuation">,</span> <span class="token variable">$calculated</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token keyword">throw</span> <span class="token keyword">new</span> <span class="token class-name">Exception</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>

        <span class="token variable">$plaintext</span> <span class="token operator">=</span> <span class="token function">unsafe_decrypt</span><span class="token punctuation">(</span><span class="token variable">$ciphertext</span><span class="token punctuation">,</span> <span class="token variable">$encKey</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token keyword">return</span> <span class="token variable">$plaintext</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
</code></pre></div>
<p>以上操作依赖了两个额外的函数。一个是用来把一个 key 分割成一个用来加密一个用来验证的两个 key。</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">splitKeys</span><span class="token punctuation">(</span><span class="token variable">$masterKey</span><span class="token punctuation">)</span>
    <span class="token punctuation">{</span>
        <span class="token keyword">return</span> <span class="token punctuation">[</span>
            <span class="token function">hash_hmac</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'ENCRYPTION'</span><span class="token punctuation">,</span> <span class="token variable">$masterKey</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
            <span class="token function">hash_hmac</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'AUTHENTICATION'</span><span class="token punctuation">,</span> <span class="token variable">$masterKey</span><span class="token punctuation">,</span> <span class="token constant boolean">true</span><span class="token punctuation">)</span>
        <span class="token punctuation">]</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
</code></pre></div>
<p>另一个是用来验证 hash 是否全等。</p>
<div class="language-php"><pre class="language-php"><code class="language-php"><span class="token keyword">function</span> <span class="token function-definition function">hashEquals</span><span class="token punctuation">(</span><span class="token variable">$a</span><span class="token punctuation">,</span> <span class="token variable">$b</span><span class="token punctuation">)</span>
    <span class="token punctuation">{</span>
        <span class="token keyword">if</span> <span class="token punctuation">(</span><span class="token function">function_exists</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'hash_equals'</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token keyword">return</span> <span class="token function">hash_equals</span><span class="token punctuation">(</span><span class="token variable">$a</span><span class="token punctuation">,</span> <span class="token variable">$b</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>
        <span class="token variable">$nonce</span> <span class="token operator">=</span> <span class="token function">openssl_random_pseudo_bytes</span><span class="token punctuation">(</span><span class="token number">32</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token keyword">return</span> <span class="token function">hash_hmac</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token variable">$a</span><span class="token punctuation">,</span> <span class="token variable">$nonce</span><span class="token punctuation">)</span> <span class="token operator">===</span> <span class="token function">hash_hmac</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'sha256'</span><span class="token punctuation">,</span> <span class="token variable">$b</span><span class="token punctuation">,</span> <span class="token variable">$nonce</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre></div>
<h2>总结<a class="header-anchor" id="%E6%80%BB%E7%BB%93" href="#%E6%80%BB%E7%BB%93">#</a></h2>
<p>这是一个相对来说特别简单的登录或者说是验证系统。主要的代码都写在了加密和解密上。对于这样一个系统，是肯定存在漏洞的，因而不能用来当作一个特别重要系统的主要验证方式。但是通过完善，还是可以看到这个系统的有效性的。</p>
<p>这个系统在浏览器无痕模式下是无法记录 Token 的，这点和正常的登录也类似。</p>
<p>这个系统不止 PHP 能实现，而能在所有的语言中实现。</p>
